@import url(https://fonts.googleapis.com/css2?family=Inconsolata&family=Karla:wght@700&display=swap);
body {
  margin: 0;
  font-family: 'Inconsolata', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  font-family: 'Karla', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: start;
  padding: 32pt;
  display: flex;
  max-width: 1000px;
}

.Title {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(24px + 3vmin);
  color: black;
  -webkit-text-decoration-color: #ffc700;
          text-decoration-color: #ffc700;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}
.Header1 {
  min-height: 7vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(18px + 3vmin);
  color: black;
}

.App-link {
  color: black;
  -webkit-text-decoration-color: #ffc700;
          text-decoration-color: #ffc700;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

