.App {
  text-align: start;
  padding: 32pt;
  display: flex;
  max-width: 1000px;
}

.Title {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(24px + 3vmin);
  color: black;
  text-decoration-color: #ffc700;
  text-decoration-line: underline;
}
.Header1 {
  min-height: 7vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(18px + 3vmin);
  color: black;
}

.App-link {
  color: black;
  text-decoration-color: #ffc700;
  text-decoration-line: underline;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
